@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
.btn-toolbar-button {
    background: none;
    border: none;
    color: #f0f0f0;
}

.btn-toolbar-button:hover {
    color: #60a0fe
}

.btn-toolbar-button:focus {
    box-shadow: none;
    outline: none;
}

.btn-mode-radio {
    padding: 0px;
    background: none;
}

.btn-mode-radio.active {
    background-color: cadetblue;
    color: white;
}

.btn-mode-radio.focus {
    box-shadow: none;
    outline: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
}

.content {
    position: fixed;
    height:100%;
    width:100%;
    display: flex;
    flex-direction: column;
}

#cyto {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

#cy-wrapper {
    display: flex;
    height: 100%;
    position: relative;
}

#layout-advanced {
    float: right;
}

/* Editor Card */
#editor {
    opacity: 75%;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 10px;
    margin-left: 10px;
    z-index: 999;
}

#editor-textarea {
    height: 100%;
}

#editor-selector {
    width:100%;
}

#editor-apply {
    color: white;
}

#editor-modes {
    width: 100%;
}

#editor-button {
    position:absolute;
    margin-top: 10px;
    margin-left: -.2em;
}

.jsoneditor-contextmenu {
    z-index: 10;
}

.autocomplete.dropdown {
    width: -webkit-max-content!important;
    width: -moz-max-content!important;
    width: max-content!important;
    max-width: 200px;
}
